.form-container {
    max-width: 350px;
    margin: 0 auto;
  }
  
  .form-container h1 {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 20px;
    color: #262626;
  }
  
  .form-container form {
    display: flex;
    flex-direction: column;
  }
  
  .form-container input[type="email"],
  .form-container input[type="password"],
  .form-container input[type="submit"] {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    background-color: #fafafa;
    font-size: 1rem;
  }
  
  .form-container input[type="submit"] {
    background-color: #3897f0;
    color: white;
    font-weight: bold;
    border: none;
    cursor: pointer;
    width: 90%;
  }
  
  .form-container input[type="submit"]:hover {
    background-color: #38a9f0;
  }
  
  .form-container input[type="email"]::placeholder,
  .form-container input[type="password"]::placeholder {
    color: #8e8e8e;
  }
  
  .form-container input[type="submit"]::placeholder {
    color: white;
  }
  